import React, { useEffect, useState } from 'react';
import { Location, Event } from '../types';
import LoadingSpinner from '../components/LoadingSpinner';
import { LeafletMap } from '../components/Map/LeafletMap';
import SearchFilters from '../components/SearchFilters';
import { useMapStore } from '../stores/mapStore';

const MapPage: React.FC = () => {
  const {
    locations,
    events,
    isLoading,
    error,
    fetchData
  } = useMapStore();

  const [selectedLocation, setSelectedLocation] = useState<Location | null>(null);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const handleLocationSelect = (location: Location) => {
    setSelectedLocation(location);
  };

  const handleEventSelect = (event: Event) => {
    // Find the location associated with the event to center the map
    const eventLocation = locations.find(l => l._id === event.location);
    if (eventLocation) {
      setSelectedLocation(eventLocation);
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-brand-dark-blue">
        <LoadingSpinner size="lg" />
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-brand-dark-blue text-red-500">
        <p>Error: {error}</p>
      </div>
    );
  }

  return (
    <div className="h-screen w-screen overflow-hidden">
      <LeafletMap
        locations={locations}
        events={events}
        selectedLocation={selectedLocation}
        onLocationSelect={handleLocationSelect}
        onEventSelect={handleEventSelect}
      />
      <div className="absolute top-20 left-1/2 -translate-x-1/2 z-[1000] w-full max-w-md px-4">
        <SearchFilters />
      </div>
    </div>
  );
};

export default MapPage;
