import { create } from 'zustand';
import { Location, Event } from '../types';
import { LocationService } from '../services/locationService';
import { EventService } from '../services/eventService';

interface CameraPreset {
  id: string;
  name: string;
  lat: number;
  lng: number;
  zoom: number;
  heading?: number;
  tilt?: number;
}

interface MapState {
  isMapLoaded: boolean;
  selectedLocation: Location | null;
  searchQuery: string;
  filteredLocations: Location[] | null;
  activeFilters: {
    category: string[];
    accessibility: boolean;
  };
  cameraPresets: CameraPreset[];
  currentPreset: string | null;
  showLayer3D: boolean;
  showLayerSatellite: boolean;
  showLayerTraffic: boolean;
  overlayConfig: {
    glbModelUrl: string | null;
    scale: number;
    rotation: { x: number; y: number; z: number };
    position: { lat: number; lng: number; altitude: number };
  };
  // New state for data handling
  locations: Location[];
  events: Event[];
  isLoading: boolean;
  error: string | null;
}

interface MapActions {
  setMapLoaded: (loaded: boolean) => void;
  setSelectedLocation: (location: Location | null) => void;
  setSearchQuery: (query: string) => void;
  setFilteredLocations: (locations: Location[] | null) => void;
  setActiveFilters: (filters: MapState['activeFilters']) => void;
  setCameraPreset: (presetId: string) => void;
  toggleLayer3D: () => void;
  toggleLayerSatellite: () => void;
  toggleLayerTraffic: () => void;
  setOverlayConfig: (config: Partial<MapState['overlayConfig']>) => void;
  // New action for fetching data
  fetchData: () => Promise<void>;
}

export const useMapStore = create<MapState & MapActions>((set, _get) => ({
  // Initial state
  isMapLoaded: false,
  selectedLocation: null,
  searchQuery: '',
  filteredLocations: null,
  activeFilters: {
    category: [],
    accessibility: false,
  },
  cameraPresets: [
    {
      id: 'campus-overview',
      name: 'Campus Overview',
      lat: 30.3557,
      lng: 76.3675,
      zoom: 16,
      heading: 0,
      tilt: 0,
    },
    {
      id: 'main-gate',
      name: 'Main Gate',
      lat: 30.3565,
      lng: 76.3660,
      zoom: 18,
      heading: 90,
      tilt: 45,
    },
    {
      id: 'academic-block',
      name: 'Academic Block',
      lat: 30.3555,
      lng: 76.3680,
      zoom: 17,
      heading: 180,
      tilt: 30,
    },
    {
      id: 'hostels',
      name: 'Hostel Area',
      lat: 30.3540,
      lng: 76.3690,
      zoom: 16,
      heading: 270,
      tilt: 15,
    },
  ],
  currentPreset: null,
  showLayer3D: false,
  showLayerSatellite: false,
  showLayerTraffic: false,
  overlayConfig: {
    glbModelUrl: null,
    scale: 1,
    rotation: { x: 0, y: 0, z: 0 },
    position: { lat: 0, lng: 0, altitude: 0 },
  },
  // New state implementation
  locations: [],
  events: [],
  isLoading: true,
  error: null,

  // Actions
  setMapLoaded: (loaded: boolean) => {
    set({ isMapLoaded: loaded });
  },

  setSelectedLocation: (location: Location | null) => {
    set({ selectedLocation: location });
  },

  setSearchQuery: (query: string) => {
    set({ searchQuery: query });
  },

  setFilteredLocations: (locations: Location[] | null) => {
    set({ filteredLocations: locations });
  },

  setActiveFilters: (filters: MapState['activeFilters']) => {
    set({ activeFilters: filters });
  },

  setCameraPreset: (presetId: string) => {
    set({ currentPreset: presetId });
  },

  toggleLayer3D: () => set((state) => ({ showLayer3D: !state.showLayer3D })),
  toggleLayerSatellite: () => set((state) => ({ showLayerSatellite: !state.showLayerSatellite })),
  toggleLayerTraffic: () => set((state) => ({ showLayerTraffic: !state.showLayerTraffic })),

  setOverlayConfig: (config: Partial<MapState['overlayConfig']>) => set((state) => ({
    overlayConfig: { ...state.overlayConfig, ...config }
  })),

  // New action implementation
  fetchData: async () => {
    try {
      set({ isLoading: true, error: null });
      const [locationsResponse, eventsResponse] = await Promise.all([
        LocationService.getLocations({ limit: 100 }),
        EventService.getUpcomingEvents(50),
      ]);
      set({
        locations: locationsResponse.locations,
        events: eventsResponse,
        isLoading: false,
      });
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to load data';
      set({ error: errorMessage, isLoading: false });
      console.error('Error in fetchData store action:', err);
    }
  },
}));
