import { Sun } from 'lucide-react';

const BottomBar = () => {
  return (
    <footer className="absolute bottom-0 left-0 right-0 z-10 bg-neutral-900/80 backdrop-blur-sm p-4">
      <div className="container mx-auto flex justify-between items-center">
        <div className="flex items-center space-x-4">
          <div className="flex items-center space-x-2">
            <Sun className="h-8 w-8 text-brand-orange" />
            <div>
              <div className="text-2xl font-bold">28°</div>
              <div className="text-xs text-neutral-400">Mon, 05 Aug, 2019</div>
            </div>
          </div>
          <div className="text-xs text-neutral-400">
            <div>NNW</div>
            <div>13 km/h</div>
          </div>
        </div>
        <div className="flex-grow mx-8">
          {/* Placeholder for scrollable POI list */}
          <div className="text-center text-neutral-500">Points of Interest will be here</div>
        </div>
        <div className="flex items-center space-x-4">
            <button className="bg-brand-orange text-white px-6 py-3 rounded-lg text-sm font-bold hover:bg-opacity-90 transition-opacity">
                GET MORE INFORMATION
            </button>
        </div>
      </div>
    </footer>
  );
};

export default BottomBar;
