import { useState, useEffect } from 'react';
import { Search, MapPin } from 'lucide-react';
import { useMapStore } from '../stores/mapStore';
import { Location } from '../types';
import { cn, debounce } from '../utils';
import { LocationService } from '../services/locationService';

interface SearchFiltersProps {
  onLocationSelect?: (location: Location) => void;
  className?: string;
}

const SearchFilters: React.FC<SearchFiltersProps> = ({
  onLocationSelect,
  className,
}) => {
  const { searchQuery, setSearchQuery } = useMapStore();
  const [localSearchQuery, setLocalSearchQuery] = useState(searchQuery);
  const [searchResults, setSearchResults] = useState<Location[]>([]);
  const [isActive, setIsActive] = useState(false);

  const debouncedSearch = debounce(async (query: string) => {
    setSearchQuery(query);
    if (query.length > 1) {
      try {
        const locs = await LocationService.searchLocations(query);
        setSearchResults(locs);
      } catch (error) {
        console.error("Search failed:", error);
        setSearchResults([]);
      }
    } else {
      setSearchResults([]);
    }
  }, 300);

  useEffect(() => {
    debouncedSearch(localSearchQuery);
  }, [localSearchQuery, debouncedSearch]);

  const handleSelect = (location: Location) => {
    if (onLocationSelect) {
      onLocationSelect(location);
    }
    setLocalSearchQuery('');
    setSearchResults([]);
    setIsActive(false);
  };

  return (
    <div className={cn("absolute top-24 left-4 z-20 w-80 text-white", className)}>
      <div className="relative">
        <Search className="absolute top-1/2 left-4 -translate-y-1/2 text-neutral-400 h-5 w-5" />
        <input
          type="text"
          placeholder="Search..."
          value={localSearchQuery}
          onChange={(e) => setLocalSearchQuery(e.target.value)}
          onFocus={() => setIsActive(true)}
          onBlur={() => setTimeout(() => setIsActive(false), 150)} // Delay to allow click
          className="w-full bg-neutral-900/80 backdrop-blur-sm border border-neutral-700 rounded-lg pl-12 pr-4 py-3 focus:ring-2 focus:ring-brand-orange focus:outline-none transition-all"
        />
      </div>
      {isActive && searchResults.length > 0 && (
        <div className="mt-2 bg-neutral-900/80 backdrop-blur-sm rounded-lg shadow-lg overflow-hidden">
          <ul className="max-h-80 overflow-y-auto divide-y divide-neutral-800">
            {searchResults.map((location) => (
              <li
                key={location._id}
                onMouseDown={() => handleSelect(location)} // Use onMouseDown to fire before blur
                className="flex items-center p-3 hover:bg-neutral-800 cursor-pointer transition-colors"
              >
                <MapPin className="h-5 w-5 mr-4 text-brand-teal flex-shrink-0" />
                <span className="truncate">{location.name}</span>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default SearchFilters;
