
const LeftSidebar = () => {
  const categories = ['ATMs', 'Bus Stops', 'Cafés', 'Emergencies', 'Museums', 'Parkings', 'Restaurants', 'Sport Centers'];

  return (
    <aside className="absolute top-1/2 left-4 -translate-y-1/2 z-10 flex items-center space-x-4">
      <div className="transform -rotate-90 whitespace-nowrap text-sm font-bold tracking-widest uppercase">
        Lifestyle
      </div>
      <div className="space-y-4">
        {categories.map((category, index) => (
          <a
            key={index}
            href="#"
            className={`block font-semibold hover:text-brand-orange transition-colors ${category === 'Cafés' ? 'text-brand-orange text-2xl' : 'text-white'}`}
          >
            {category}{category === 'Cafés' && <span className="text-sm align-top">(3)</span>}
          </a>
        ))}
      </div>
    </aside>
  );
};

export default LeftSidebar;
